import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './ResetPassword.css';
import FloatingCharacter from './FloatingCharacter';

const ResetPassword = ({ onResetSuccess }) => {
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [passwordStrength, setPasswordStrength] = useState(0);

  // Extract token from URL query parameter
  const getTokenFromUrl = () => {
    const params = new URLSearchParams(window.location.search);
    return params.get('token');
  };

  const token = getTokenFromUrl();

  useEffect(() => {
    if (!token) {
      setError(' Link khng hp l hoc  ht hn');
    }
  }, [token]);

  // Calculate password strength
  useEffect(() => {
    if (newPassword.length === 0) {
      setPasswordStrength(0);
    } else {
      let strength = 0;
      if (newPassword.length >= 6) strength += 25;
      if (newPassword.length >= 10) strength += 25;
      if (/[a-z]/.test(newPassword)) strength += 15;
      if (/[A-Z]/.test(newPassword)) strength += 15;
      if (/[0-9]/.test(newPassword)) strength += 10;
      if (/[!@#$%^&*]/.test(newPassword)) strength += 10;
      setPasswordStrength(Math.min(strength, 100));
    }
  }, [newPassword]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!newPassword) {
      setError('Vui lng nhp mt khu mi');
      return;
    }

    if (newPassword.length < 6) {
      setError('Mt khu phi t nht 6 k t');
      return;
    }

    if (newPassword !== confirmPassword) {
      setError('Mt khu khng trng khp');
      return;
    }

    setLoading(true);

    try {
      await axios.post('http://localhost:8000/api/auth/reset-password', {
        token: token,
        new_password: newPassword
      });

      setSuccess(' Mt khu  c t li thnh cng!');

      setTimeout(() => {
        onResetSuccess();
      }, 2000);

    } catch (err) {
      setError(err.response?.data?.detail || 'Li t li mt khu');
    } finally {
      setLoading(false);
    }
  };

  const getPasswordStrengthLabel = () => {
    if (passwordStrength < 25) return 'Yu';
    if (passwordStrength < 50) return 'Va';
    if (passwordStrength < 75) return 'Kh';
    return 'Mnh';
  };

  const getPasswordStrengthColor = () => {
    if (passwordStrength < 25) return '#FF6B6B';
    if (passwordStrength < 50) return '#FFD93D';
    if (passwordStrength < 75) return '#6BCB77';
    return '#4ECDC4';
  };

  if (!token) {
    return (
      <div className="reset-password-container">
        <div className="reset-password-box">
          <div className="reset-password-header error-header">
            <h1> Link Khng Hp L</h1>
          </div>
          <div className="reset-password-content">
            <p className="error-message">Link reset mt khu khng hp l hoc  ht hn (24 gi)</p>
            <button 
              onClick={() => window.location.href = '/login'}
              className="btn-back"
            >
               Quay li ng nhp
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="reset-password-container">
      <div className="reset-password-box">
        <FloatingCharacter />
        {/* Header */}
        <div className="reset-password-header">
          <h1> t Li Mt Khu</h1>
        </div>

        {/* Content */}
        <div className="reset-password-content">
          {success ? (
            <div className="reset-success">
              <div className="success-icon"></div>
              <h2>Mt khu  c t li!</h2>
              <p>Bn s c chuyn hng n trang ng nhp trong vi giy</p>
              <button 
                onClick={onResetSuccess}
                className="btn-primary"
              >
                 ng nhp ngay
              </button>
            </div>
          ) : (
            <>
              <p className="reset-subtitle">
                Nhp mt khu mi ca bn
              </p>

              <form onSubmit={handleSubmit} className="reset-form">
                {/* Password Input */}
                <div className="form-group">
                  <label className="form-label">
                    <span className="label-icon"></span>
                    Mt khu mi
                  </label>
                  <div className="password-input-wrapper">
                    <input
                      type={showPassword ? "text" : "password"}
                      value={newPassword}
                      onChange={(e) => setNewPassword(e.target.value)}
                      placeholder="Nhp mt khu mi (ti thiu 6 k t)"
                      className="form-input"
                      disabled={loading}
                    />
                    <button
                      type="button"
                      className="password-toggle"
                      onClick={() => setShowPassword(!showPassword)}
                      disabled={loading}
                    >
                      {showPassword ? '' : ''}
                    </button>
                  </div>

                  {/* Password Strength Meter */}
                  {newPassword && (
                    <div className="password-strength">
                      <div className="strength-bar">
                        <div 
                          className="strength-fill"
                          style={{
                            width: `${passwordStrength}%`,
                            backgroundColor: getPasswordStrengthColor()
                          }}
                        ></div>
                      </div>
                      <span className="strength-label">
                         mnh: <strong style={{ color: getPasswordStrengthColor() }}>
                          {getPasswordStrengthLabel()}
                        </strong>
                      </span>
                    </div>
                  )}
                </div>

                {/* Confirm Password Input */}
                <div className="form-group">
                  <label className="form-label">
                    <span className="label-icon"></span>
                    Xc nhn mt khu
                  </label>
                  <input
                    type={showPassword ? "text" : "password"}
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    placeholder="Nhp li mt khu"
                    className="form-input"
                    disabled={loading}
                  />
                  {confirmPassword && newPassword === confirmPassword && (
                    <p className="success-text"> Mt khu trng khp</p>
                  )}
                  {confirmPassword && newPassword !== confirmPassword && (
                    <p className="error-text"> Mt khu khng trng khp</p>
                  )}
                </div>

                {error && <div className="alert alert-error">{error}</div>}

                <button
                  type="submit"
                  className="btn-primary"
                  disabled={loading || !newPassword || !confirmPassword}
                >
                  {loading ? ' ang cp nht...' : ' t li mt khu'}
                </button>
              </form>

              <div className="password-requirements">
                <h3> Yu cu mt khu:</h3>
                <ul>
                  <li className={newPassword.length >= 6 ? 'met' : ''}>
                    {newPassword.length >= 6 ? '' : ''} t nht 6 k t
                  </li>
                  <li className={/[A-Z]/.test(newPassword) ? 'met' : ''}>
                    {/[A-Z]/.test(newPassword) ? '' : ''} Cha ch hoa (A-Z)
                  </li>
                  <li className={/[a-z]/.test(newPassword) ? 'met' : ''}>
                    {/[a-z]/.test(newPassword) ? '' : ''} Cha ch thng (a-z)
                  </li>
                  <li className={/[0-9]/.test(newPassword) ? 'met' : ''}>
                    {/[0-9]/.test(newPassword) ? '' : ''} Cha s (0-9)
                  </li>
                </ul>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default ResetPassword;
