import React, { useState } from 'react';
import axios from 'axios';
import './ForgotPassword.css';
import FloatingCharacter from './FloatingCharacter';

const ForgotPassword = ({ onBackToLogin }) => {
  const [email, setEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [step, setStep] = useState('email'); // email or sent

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!email.trim()) {
      setError('Vui lng nhp email');
      return;
    }

    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      setError('Email khng hp l');
      return;
    }

    setLoading(true);

    try {
      await axios.post('http://localhost:8000/api/auth/forgot-password', {
        email: email.trim()
      });

      setStep('sent');
      setSuccess(' Link reset mt khu  c gi!');

    } catch (err) {
      setError(err.response?.data?.detail || 'Li gi link reset');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="forgot-password-container">
      <div className="forgot-password-box">
        <FloatingCharacter />
        {/* Header */}
        <div className="forgot-password-header">
          <button 
            className="back-btn-header"
            onClick={onBackToLogin}
            title="Quay li"
          >
            
          </button>
          <h1> Ly Li Mt Khu</h1>
          <div style={{ width: '40px' }}></div>
        </div>

        {/* Content */}
        {step === 'email' ? (
          <>
            <div className="forgot-password-content">
              <p className="forgot-password-subtitle">
                Nhp email ca bn v chng ti s gi link  t li mt khu
              </p>

              <form onSubmit={handleSubmit} className="forgot-password-form">
                <div className="form-group">
                  <label htmlFor="email" className="form-label">
                    <span className="label-icon"></span>
                    Email ng k
                  </label>
                  <input
                    id="email"
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="your@email.com"
                    className="form-input"
                    disabled={loading}
                  />
                </div>

                {error && <div className="alert alert-error">{error}</div>}

                <button
                  type="submit"
                  className="btn-primary"
                  disabled={loading}
                >
                  {loading ? ' ang gi...' : ' Gi link reset'}
                </button>
              </form>

              <div className="info-box">
                <h3> Thng tin</h3>
                <ul>
                  <li>Link reset s c gi trong vi giy</li>
                  <li>Link hp l trong 24 gi</li>
                  <li>Kim tra c th mc spam nu khng thy</li>
                </ul>
              </div>
            </div>

            <button 
              className="btn-back"
              onClick={onBackToLogin}
              disabled={loading}
            >
               Quay li ng nhp
            </button>
          </>
        ) : (
          <>
            <div className="forgot-password-success">
              <div className="success-icon"></div>
              <h2>Email  c gi!</h2>
              <p className="success-message">
                Chng ti  gi link t li mt khu n:
              </p>
              <p className="success-email">{email}</p>
              
              <div className="success-steps">
                <h3> Cc bc tip theo:</h3>
                <ol>
                  <li>Kim tra email ca bn</li>
                  <li>Nhp vo link "t li mt khu"</li>
                  <li>Nhp mt khu mi ca bn</li>
                  <li>ng nhp vi mt khu mi</li>
                </ol>
              </div>

              <div className="warning-box">
                <p> <strong>Khng thy email?</strong></p>
                <p>Kim tra th mc spam hoc promotions</p>
              </div>
            </div>

            <button 
              className="btn-back"
              onClick={onBackToLogin}
            >
               Quay li ng nhp
            </button>
          </>
        )}
      </div>
    </div>
  );
};

export default ForgotPassword;
